<template>
  <div class="user">
    <v-container>
      <v-row>
        <v-col>
          <h1 class="text-center my-10"> </h1>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12">
          <v-card width="75%" class="mx-auto mb-10">
            <v-card-title class="headline text-center my-4 font-weight-bold"> </v-card-title>
            <v-card-text>
              <v-list>
                <v-list-item-content>
                    <v-list-item-title><strong>  : </strong> {{ userName }}</v-list-item-title>
                    <v-list-item-subtitle class="my-5"><strong> : </strong> {{ userEmail }}</v-list-item-subtitle>
                    <v-list-item-title class="mb-7"><strong>  : </strong> {{ studyGroupName }}</v-list-item-title>
                    <p class="text-sm text-muted font-weight-bold" style="color: red;">*     !</p>
                  </v-list-item-content>
              </v-list>
            </v-card-text>
          </v-card>
          <v-card width="75%" class="mx-auto">
            <v-card-title class="headline text-center my-4 font-weight-bold"> </v-card-title>
            <v-card-text>
              <v-form>
                
                <p class="text-center">
                      .  ?
                </p>

                <div class="text-center mt-7 mb-3">
                  <v-btn
                    color="secondary"
                    @click="sendResetEmail"
                  >
                       
                  </v-btn>
                </div>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { auth } from '@/firebase'
import { ref, onMounted } from 'vue';
import {  sendPasswordResetEmail } from 'firebase/auth';
import { getFirestore, doc, getDoc } from 'firebase/firestore';

export default {
  name: 'UserView',
  setup() {
    const userName = ref('');
    const userEmail = ref('');    
    const studyGroupName = ref('');
    const db = getFirestore();

    //   
    const fetchUserData = async () => {
      if (auth.currentUser) {
        const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
        if (userDoc.exists()) {
          const userData = userDoc.data();
          userName.value = userData.name;
          userEmail.value = userData.email;
          studyGroupName.value = userData.group;
        } else {
          console.log('No such document!');
        }
      }
    };

    //    
    const sendResetEmail = async () => {
      try {
        await sendPasswordResetEmail(auth, userEmail.value);
        alert('   .  .');
      } catch (error) {
        alert(`    : ${error.message}`);
      }
    };

    // onMounted 
    onMounted(() => {
      fetchUserData();
    });

    return { userName, userEmail, studyGroupName, sendResetEmail };
  }
}
</script>

<style scoped lang="scss">
.user .v-card{
  margin-bottom: 100px !important;
}
</style>
