<template>
  <div class="login">
    <v-container>
      <v-row justify="center">
        <v-col>
          <v-card>
            <v-card-title class="headline text-center font-weight-bold my-4"></v-card-title>
            <v-card-text>
              <v-form>
                <v-text-field
                  label=""
                  v-model="email"
                  prepend-icon="mdi-account"
                  variant="outlined"
                  type="email"
                  required
                ></v-text-field>
                <v-text-field
                  label=""
                  v-model="password"
                  prepend-icon="mdi-lock"
                  variant="outlined"
                  type="password"
                  required
                ></v-text-field><v-text-field
                  label=" "
                  v-model="groupName"
                  prepend-icon="mdi-account-multiple"
                  variant="outlined"
                  type="text"
                  required
                ></v-text-field>
                <div class="text-center mt-7 mb-3">
                  <v-btn
                    :disabled="!valid"
                    color="primary"
                    class="mr-4"
                    @click="login"
                  >
                    
                  </v-btn>
                  <v-btn
                    color="info"
                    @click="reset"
                  >
                    
                  </v-btn>
                </div>
              </v-form>
            </v-card-text>
            <v-card-actions class="justify-center">
              <v-btn text color="primary" @click="switchToSignUp">
                 ? 
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { auth, db } from '@/firebase'
import { ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import { signInWithEmailAndPassword } from 'firebase/auth';
import { query, where, getDocs, collection } from 'firebase/firestore';
import { useStore } from 'vuex';      // Vuex   <= 

export default {
  name: 'LoginView',
  setup() {
    const email = ref('');
    const password = ref('');
    const groupName = ref('');
    const router = useRouter();
    const store = useStore();         // Vuex        <= 

    const valid = computed(() => {
      return email.value.includes('@') && password.value.length >= 6;
    });

    const login = async () => {
      try {
        // Firestore 'users'       
        const usersRef = collection(db, 'users');
        const q = query(usersRef, where('email', '==', email.value));
        const querySnapshot = await getDocs(q);

        // console.log(`   : ${querySnapshot.size}`);
        // querySnapshot.forEach(doc => {
        //   console.log(` ID: ${doc.id}, : `, doc.data());
        // });

        let groupExists = false;

        querySnapshot.forEach(doc => {
          const userData = doc.data();
          // console.log(` : ${JSON.stringify(userData)}`);
          if (userData.group && Array.isArray(userData.group) && userData.group.includes(groupName.value)) {
            groupExists = true;
          }
        });

        if (!groupExists) {
          //  groupName  
          alert('     !');
          return;
        }

        //     
        await signInWithEmailAndPassword(auth, email.value, password.value);

        //    Vuex           <= 
        store.dispatch('login');

        alert(' !');
        router.push({ name: 'home' });
      } catch (error) {
        let errorMessage = ' .';
        if (error.code === 'auth/email-already-in-use') {
          errorMessage = '   .   .';
        } else if (error.code === 'auth/weak-password') {
          errorMessage = '  6  .';
        } else if (error.code === 'auth/invalid-email') {
          errorMessage = '  .';
        }
        alert(errorMessage);
      }
    };

    const reset = () => {
      email.value = '';
      password.value = '';
    };

    const switchToSignUp = () => {
      router.push({ name: 'signup' });
    };

    return { email, password, valid, groupName, login, reset, switchToSignUp };
  }
}
</script>

<style scoped lang="scss">
  .login .v-card{
    width: 55%;
    margin-top: 60px;
    margin: 150px auto 100px;
  }

  @media screen and (max-width: 768px) {
    .login .v-card{
      width: 80%;
      margin: 60px auto 100px;
    }
  }

  @media screen and (max-width: 480px) {
    .login .v-card{
      width: 90%;
    }
  }
</style>
