<template>
  <div class="word-meditation">
    <v-container>
      <v-row>
        <v-col>
          <h1 class="text-center my-10"> </h1>
          <p class="text-center mb-12">              .</p>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-card width="100%" class="mx-auto pa-0 rounded-lg" variant="outlined">
            <v-list lines="two">
              <v-list-item
                v-for="item in paginatedItems"
                :key="item.id"
                @click="openModal(item)"
              >
                <template v-slot:prepend>
                  <v-avatar color="amber">
                    <v-icon color="white">mdi-clipboard-text</v-icon>
                  </v-avatar>
                </template>

                <template v-slot:default>
                  <v-list-item-content class="d-flex flex-column">
                    <v-list-item-title>
                      {{ item.title.replace(/<\/?[^>]+(>|$)/g, "") }}
                    </v-list-item-title>
                    <v-list-item-subtitle class="text-left hidden-md-and-down" style="font-size: 16px;">
                      {{ (item.body.length > 30 ? item.body.substring(0, 50) + '...' : item.body).replace(/<\/?[^>]+(>|$)/g, "") }}
                    </v-list-item-subtitle>
                    <v-list-item-subtitle class="text-left hidden-md-and-up" style="font-size: 16px;">
                      {{ (item.body.length > 30 ? item.body.substring(0, 20) + '...' : item.body).replace(/<\/?[^>]+(>|$)/g, "") }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </template>
                
                <template v-slot:append>
                  <v-list-item-subtitle>{{ item.createAt.toDate().toLocaleDateString() }}</v-list-item-subtitle>                  
                </template>
              </v-list-item>
            </v-list>            
          </v-card>          
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-pagination
            v-model="page"
            :length="totalPages"
            :total-visible="7"
            color="primary"
            class="text-center hidden-md-and-down"
          ></v-pagination>
          <v-pagination
            v-model="page"
            :length="totalPages"
            :total-visible="4"
            color="primary"
            class="text-center hidden-md-and-up"
          ></v-pagination>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog v-model="modalOpen" width="100%">
      <v-card class="overflow-y-auto rounded-lg">
        <div class="text-right">
          <v-btn size="small" icon @click="modalOpen = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
        <v-card-title class="my-3 text-center">
          <input v-model="title" class="text-center w-100 editable" height="auto">
        </v-card-title>
        <v-card-subtitle class="text-right mb-10">
          {{ selectedItem.createAt.toDate().toLocaleDateString() }}
        </v-card-subtitle>
        <v-card-text>
          <textarea :rows="calculateRows(body)" v-model="body" class="w-100 editable"></textarea>
        </v-card-text>
        <v-card-actions class="mt-15 d-flex flex-column justify-center"  v-if="isAdmin">
          <div class="mb-5">
            <v-btn
              size="small"
              color="primary"
              icon="mdi-pencil"
              @click.stop="editItem(item)"
            ></v-btn>
            <v-btn
              class="mx-3"
              size="small"
              color="error"
              icon="mdi-delete"
              @click.stop="deleteItem()"
            ></v-btn>          
            <v-btn
              size="small"
              color="grey"
              icon="mdi-cancel"
              @click.stop="cancelEdit(item)"
            ></v-btn>
          </div>
        </v-card-actions>          
      </v-card>
    </v-dialog>
  </div>
</template>


<script>
import { ref, computed, onMounted} from 'vue';
import { db, auth } from '@/firebase'; 
import { collection, query, where, getDocs, doc, updateDoc, deleteDoc, orderBy } from 'firebase/firestore';
import { onAuthStateChanged } from 'firebase/auth';

export default {
  name: 'WordMeditation', 
  methods: {
    calculateRows(text) {
      const lines = text.split('\n');
      let totalRows = lines.length;
      for (let line of lines) {
        totalRows += Math.floor(line.length / 120); // 80    
      }
      return Math.max(15, totalRows); //  15 
    },
    adjustTextareaHeight(event) {
      const textarea = event.target;
      textarea.style.height = 'auto'; //   
      textarea.style.height = textarea.scrollHeight + 'px'; //    
    }
  },
  setup() {
    const studyItems = ref([]);
    const searchQuery = ref('');
    const modalOpen = ref(false);
    const selectedItem = ref({});
    const userEmail = ref('');
    const page = ref(1);
    const itemsPerPage = 10;
    const title = ref('');
    const body = ref('');
    const tags = ref('');    
    const isAdmin = ref(false);


    //   
    async function fetchStudyItems() {
      //  
      title.value = '';
      body.value = '';

      const q = query(
        collection(db, 'blogs'),
        where('category', '==', ''), // 'category'  
        orderBy('createAt', 'desc') //  
      );

      const querySnapshot = await getDocs(q);
      studyItems.value = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    }

    //        
    function openModal(item) {
      selectedItem.value = item;
      title.value = item.title; //    title ref 
      body.value = item.body; //    body ref 
      modalOpen.value = true;
    }

    //    
    function updateTitle(event) {
      title.value = event.target.innerText;
    }
    
    function updateBody(event) {
      body.value = event.target.innerText;
    }

    //    
    async function editItem() {
      const blogDoc = doc(db, 'blogs', selectedItem.value.id); // 'posts' 'blogs' 
      await updateDoc(blogDoc, {
        title: title.value, // title ref   
        body: body.value // body ref   
      });
      fetchStudyItems(userEmail.value); //    
      modalOpen.value = false; //  
    }

    //    
    async function deleteItem() {
      const item = selectedItem.value;
      if (!item || !item.id) {
        console.error("   ID .");
        return;
      }
      const blogDoc = doc(db, 'blogs', item.id); // 'posts' 'blogs' 
      await deleteDoc(blogDoc);
      fetchStudyItems(userEmail.value); //    
      modalOpen.value = false; //  
    }

    //  
    function cancelEdit() {
      modalOpen.value = false;
    }

    //      
    const filteredStudyItems = computed(() => {
      if (searchQuery.value) {
        const searchTerms = searchQuery.value.toLowerCase().split(/\s*,\s*|\s+/);
        return studyItems.value.filter(item => {
          return searchTerms.some(term =>
            item.title.toLowerCase().includes(term) ||
            item.body.toLowerCase().includes(term) || 
            (Array.isArray(item.tags) && item.tags.some(tag => tag.toLowerCase().includes(term)))
          );
        });
      }
      return studyItems.value;
    });

    // Paginate filtered items
    const paginatedItems = computed(() => {
      const start = (page.value - 1) * itemsPerPage;
      const end = start + itemsPerPage;
      const items = filteredStudyItems.value.slice(start, end);
      return items;
    });

    // Calculate total pages for pagination
    const totalPages = computed(() => {
      return Math.ceil(filteredStudyItems.value.length / itemsPerPage);
    });

    //    
    function resetSearch(){
      searchQuery.value = '';
    }

    //        
    onMounted(() => {
      onAuthStateChanged(auth, (user) => {
        if (user) {
          userEmail.value = user.email;
          isAdmin.value = user.email === 'admin@gmail.com'; //    
          fetchStudyItems();
        } else {
          //     
        }
      });

    });

    return {
      studyItems,
      searchQuery,
      modalOpen,
      selectedItem,
      filteredStudyItems,
      paginatedItems,
      page,
      totalPages,
      title,
      body,
      tags,
      isAdmin,
      openModal,
      updateTitle,
      updateBody,
      editItem,
      deleteItem,
      cancelEdit,
      resetSearch
    };
  }
}
</script>


<style scoped lang="scss">    
  @media (max-width: 768px) { /*      */
    textarea.editable {
      width: 100%; /*    */
      min-height: 150px; /*    */
    }
  }
</style>


