<template>
  <div class="signup">
    <v-container>
      <v-row justify="center">
        <v-col cols="12">
          <v-card>
            <v-card-title class="headline text-center font-weight-bold my-4"></v-card-title>
            <v-card-text>
              <v-form>
                <v-text-field
                  label=" "
                  v-model="name"
                  prepend-icon="mdi-account"
                  variant="outlined"
                  type="text"
                  required
                ></v-text-field>
                <v-text-field
                  label=""
                  v-model="email"
                  prepend-icon="mdi-email"
                  variant="outlined"
                  type="email"
                  required
                ></v-text-field>
                <v-text-field
                  label=""
                  v-model="password"
                  prepend-icon="mdi-lock"
                  variant="outlined"
                  type="password"
                  required
                ></v-text-field>
                <v-text-field
                  label=" "
                  v-model="groupName"
                  prepend-icon="mdi-account-multiple"
                  variant="outlined"
                  type="text"
                  required
                ></v-text-field>
                <div class="text-center mt-7 mb-3">
                  <v-btn
                    :disabled="!valid"
                    color="primary"
                    class="mr-4"
                    @click="handleSignup"
                  >
                    
                  </v-btn>
                  <v-btn
                    color="info"
                    @click="reset"
                  >
                    
                  </v-btn>
                </div>
              </v-form>
            </v-card-text>
            <v-card-actions class="justify-center">
              <v-btn text color="primary" @click="switchToLogin">
                  ? 
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { auth, db } from '@/firebase'
import { ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import { createUserWithEmailAndPassword } from 'firebase/auth'
import { doc, setDoc } from 'firebase/firestore'

export default {
  name: 'SignUpView',
  setup() {
    const ADMIN_EMAIL = "admin@gmail.com"; //    
    const name = ref('');
    const email = ref('');
    const password = ref('');
    const groupName = ref([]);
    const router = useRouter();

    const valid = computed(() => {
      return email.value.includes('@') && password.value.length >= 6;
    });

    const handleSignup = async () => {
      try {
        // 1.  groupName    
        const userCredential = await createUserWithEmailAndPassword(auth, email.value, password.value);
        const user = userCredential.user;

        // 3. Firestore   
        const userDoc = {
          name: name.value, 
          email: email.value,
          // password: password.value,
          group: [groupName.value], // groupName  
          role: email.value === ADMIN_EMAIL ? 'admin' : 'user' // role 
        };
        
        await setDoc(doc(db, 'users', user.uid), userDoc);

        alert(' !');
        router.push({ name: 'home' });
      } catch (error) {
        // alert(` : ${error.message}`);
        let errorMessage = ' .'
        if (error.code === 'auth/email-already-in-use') {
          errorMessage = '   .   .'
        } else if (error.code === 'auth/weak-password') {
          errorMessage = '  6  .'
        } else if (error.code === 'auth/invalid-email') {
          errorMessage = '  .'
        }
        alert(errorMessage)
      }
    };

    const reset = () => {
      name.value = '';
      email.value = '';
      password.value = '';
      groupName.value = '';
    };

    const switchToLogin = () => {
      router.push({ name: 'login' });
    };

    return { name, email, password, groupName, valid, handleSignup, reset, switchToLogin };
  }
}
</script>



<style scoped lang="scss">
  .signup .v-card{
    width: 55%;
    margin: 150px auto 100px;
  }
  
  @media screen and (max-width: 768px) {
    .signup .v-card{
      width: 80%;
      margin: 60px auto 100px;
    }
  }

  @media screen and (max-width: 480px) {
    .signup .v-card{
      width: 90%;
    }
  }
</style>
