<template>
  <div class="search-word">
    <v-container>
      <v-row>
        <v-col cols="12">
          <v-form @submit.prevent="search" class="d-flex">
            <v-text-field
              v-model="searchTerm"
              label=" "
              width="85%"
              single-line
              hide-details
              class="mx-auto mb-10"
              variant="outlined"
              append-inner-icon="mdi-magnify"
              rounded
              required
            ></v-text-field>  
            <v-btn
              @click="resetSearch"
              class="rounded-lg ml-2"
              variant="tonal"
              size="x-large"
            ></v-btn>              
          </v-form>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-card class="rounded-lg" variant="outlined">
            <v-card-text>
              <div v-if="paginatedResults.length">
                <v-list>
                  <v-list-item v-for="(result, index) in paginatedResults" :key="index">
                    <v-list-item-content>
                      <v-list-item-title>{{ result.reference }}</v-list-item-title>
                      <v-list-item-subtitle>{{ result.text }}</v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
                <v-pagination
                  v-model="page"
                  :length="totalPages"
                  :total-visible="7"
                  color="primary"
                  class="text-center hidden-md-and-down"
                ></v-pagination>
                <v-pagination
                  v-model="page"
                  :length="totalPages"
                  :total-visible="4"
                  color="primary"
                  class="text-center hidden-md-and-up"
                ></v-pagination>
              </div>
              <div v-else>
                  .
              </div>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios'; // axios  

export default {
  name: 'SearchWord',
  data() {
    return {
      searchTerm: '', //    
      results: [], //   
      page: 1, //   
      itemsPerPage: 10, //    
      selectedBook: '', //  
      selectedChapter: '', //  
      bookNames: [], //   
      chapters: {}, //    
      bibleData: {}, //   
    };
  },
  computed: {
    filteredResults() {
      let results = [];
      if (this.selectedBook && this.selectedChapter) {
        //      
        const filteredEntries = this.bibleData.filter(entry =>
          entry.book === this.selectedBook && entry.chapter === this.selectedChapter
        );
        results = filteredEntries.map(entry => ({
          reference: `${entry.book} ${entry.chapter}:${entry.verse}`,
          text: entry.text
        }));
      } else if (this.searchTerm) {
        //     
        results = this.bibleData.filter(entry =>
          entry.text.includes(this.searchTerm)
        ).map(entry => ({
          reference: `${entry.book} ${entry.chapter}:${entry.verse}`,
          text: entry.text
        }));
      }
      return results.slice(0, 2000); //  2000  
    },
    totalPages() {
      return Math.ceil(this.filteredResults.length / this.itemsPerPage); //    
    },
    paginatedResults() {
      const start = (this.page - 1) * this.itemsPerPage; //   
      const end = start + this.itemsPerPage; //   
      return this.filteredResults.slice(start, end); //    
    },
  },
  methods: {
    async loadBibleData() {
      try {
        const response = await axios.get('/db/bible.json'); //    
        this.bibleData = response.data; //   
        //    
        this.bookNames = [...new Set(this.bibleData.map(item => item.book))];
      } catch (error) {
        console.error('Error loading Bible data:', error); //      
      }
    },
    search() {
      this.page = 1; //     
      this.results = this.searchBible(this.searchTerm); //   
    },
    searchBible(searchTerm) {
      return this.bibleData.filter(entry => entry.text.includes(searchTerm)).map(entry => ({
        reference: `${entry.book} ${entry.chapter}:${entry.verse}`,
        text: entry.text
      }));
    },
    fetchChapters() {
      if (this.selectedBook) {
        const chapters = this.bibleData.filter(item => item.book === this.selectedBook)
          .map(item => item.chapter);
        this.chapters[this.selectedBook] = [...new Set(chapters)];
      } else {
        this.chapters[this.selectedBook] = [];
      }
      this.selectedChapter = ''; //   
    },
    resetSearch() {
      this.searchTerm = ''; //  
      this.selectedBook = ''; //   
      this.selectedChapter = ''; //   
      this.results = []; //  
      this.page = 1; //   
    },
  },
  mounted() {
    this.loadBibleData(); //      
  },
};
</script>

<style scoped lang="scss">
  .search-word .v-container .v-card{
    padding: 50px 20px !important;
    margin-bottom: 100px;

    .v-pagination{
      margin-bottom: 0 !important;
    }
  }
</style>
