<template>
  <v-layout>
    <!-- App Bar : Header Area -->
    <v-app-bar :elevation="2" color="blue" class="pr-4 d-flex justify-space-between" v-if="currentView !== 'IntroView'">
      <div class="logo flex-grow-1 d-flex align-items-center justify-content-start">
        <v-app-bar-nav-icon class="hidden-md-and-up ml-2" @click="drawer = !drawer" v-if="isLoggedIn"></v-app-bar-nav-icon>
        <router-link to="/home" class="text-white text-decoration-none">
          <v-app-bar-title class="py-3">BIBLE GROUPS</v-app-bar-title>
        </router-link>
      </div>
      <nav class="gnb hidden-sm-and-down flex-grow-1">
        <template v-if="isLoggedIn">
          <router-link v-for="menu in menus" :key="menu.name" :to="menu.path">
            {{ menu.name }}
          </router-link>
        </template>
      </nav>
      <div class="icon-group flex-grow-1 text-right">
        <span class="welcome-message mr-2 hidden-md-and-down" v-if="isLoggedIn && user.email">{{ user.name }}, !</span>
        <v-btn to="/home" class="ml-4" v-if="!isLoggedIn">
          <v-icon size="25">mdi-home</v-icon>
        </v-btn>
        <v-btn to="/admin" v-if="isLoggedIn && role === 'admin'"><v-icon size="25">mdi-cog</v-icon></v-btn>
        <v-btn to="/user" v-if="isLoggedIn && role !== 'admin'"><v-icon size="25">mdi-account-cog</v-icon></v-btn>
        <v-btn @click="handleLogout" v-if="isLoggedIn">
          <v-icon size="26">mdi-account-arrow-right-outline</v-icon>
        </v-btn>
        <v-btn to="/login" v-else>
          <v-icon size="26">mdi-account-arrow-left-outline</v-icon>
        </v-btn>
        <v-btn to="/signup" v-if="!isLoggedIn">
          <v-icon size="23">mdi-auto-fix</v-icon>
        </v-btn>
      </div>
    </v-app-bar>

    <!-- App Bar : Sidebar Area -->
    <v-navigation-drawer color="grey-lighten-4" class="text-start hidden-md-and-up" v-model="drawer" v-if="isLoggedIn">
      <v-list nav class="sidebar-menu-items">
        <v-list-item v-for="(link, index) in links" :key="index" :to="link.path" class="cursor-pointer">
          <v-icon class="mr-5">{{ link.icon }}</v-icon> <span class="font-weight-bold">{{ link.name }}</span>
        </v-list-item>
      </v-list>
      <v-divider></v-divider>
      <v-list v-if="role === 'admin'">
        <v-list-item prepend-icon="mdi-account-circle">USERS</v-list-item>
        <v-list-item><span class="welcome-message">.<br> {{ user.name }}!</span></v-list-item>
        <v-list-item><v-btn to="/admin">  <v-icon>mdi-link-variant</v-icon></v-btn></v-list-item>
      </v-list>
      <v-list v-else>
        <v-list-item prepend-icon="mdi-account-circle">USERS</v-list-item>
        <v-list-item><span class="welcome-message" v-if="isLoggedIn && user.email">{{ user.name }}<br>!</span></v-list-item>
      </v-list>  
    </v-navigation-drawer>

    <!-- App Bar : Footer Area -->
    <v-footer class="bg-grey-lighten-4 flex-column" :app="true">
      <p class="py-5 font-weight-medium">
        Copyright &copy; 2024. All rights reserved.
      </p>
    </v-footer>
  </v-layout>

  <router-view/>
  
</template>

<script>
import { auth, db } from '@/firebase';
import { ref, onMounted, onBeforeUnmount } from 'vue';
import { useRouter } from 'vue-router';
import { onAuthStateChanged, signOut } from 'firebase/auth';
import { doc, getDoc } from 'firebase/firestore';

export default {
  name: 'DefaultLayout',
  data: () => ({ 
    open: [],    
  }),
  setup() {
    const router = useRouter();
    const user = ref(null);
    const isLoggedIn = ref(false);
    const drawer = ref(false);
    const role = ref('');

    const links = ref([
      { name: '', path: '/home',  icon: 'mdi-home' },
      { name: '', path: '/about', icon: 'mdi-account' },
      { name: '', path: '/study', icon: 'mdi-book-open' },
      { name: '', path: '/search', icon: 'mdi-magnify' },
      { name: '', path: '/resource', icon: 'mdi-magnify' },
      { name: '', path: '/manual', icon: 'mdi-help' },
    ]);
    const menus = ref([
      { name: '', path: '/home', active: false },
      { name: '', path: '/about', active: false },
      { name: '', path: '/study', active: false },
      { name: '', path: '/search', active: false },
      { name: '', path: '/resource', icon: 'mdi-magnify' },
      { name: '', path: '/manual', active: false },
    ]);

    /**
     * Firebase      
     *
     * 1. Firebase       /  .
     * 2.   `user.value`    `isLoggedIn.value` true .
     * 3.   `user.value` null  `isLoggedIn.value` false .
     * 4.        .
     */

    const fetchUserDetails = async (uid) => {
      const userDoc = await getDoc(doc(db, 'users', uid));
      if (userDoc.exists()) {
        const userData = userDoc.data();
        role.value = userData.role; //  
        user.value.name = userData.name; //  
      } else {
        role.value = 'user'; //   
        user.value.name = ''; //   
      }
    };


    const initFirebaseAuth = () => {
      const unsubscribe = onAuthStateChanged(auth, async (firebaseUser) => {
        if (firebaseUser) {
          user.value = firebaseUser;
          isLoggedIn.value = true;
          await fetchUserDetails(firebaseUser.uid);
        } else {
          user.value = null;
          isLoggedIn.value = false;
          role.value = null;
        }
      });

      onBeforeUnmount(unsubscribe);
    };

    //  : auth  
    /**
     *    
     *
     * 1. Firebase      .
     * 2.    `user.value` null  `isLoggedIn.value` false .
     * 3.    '/home'  .
     * 4.       .
     */
    const handleLogout = async () => {
      try {
        await signOut(auth);
        user.value = null;
        isLoggedIn.value = false;
        router.push('/');
      } catch (error) {
        console.error(' :', error);
      }
    };

    onMounted(() => {
      initFirebaseAuth();
    });

    return { drawer, links, menus, isLoggedIn, user, role, initFirebaseAuth, handleLogout };
  }
}
</script>

<style>
.v-app-bar{
  height: 70px;
}

.v-footer{
  height: 80px;
}

/* Container */
.v-container {
  /* Card Area */
  .v-card{
    border: 1px solid #dbdbdb;

    .v-list-item{
      margin: 10px 0;      
    }
  }
}

@media screen and (min-width: 1025px) {
  .v-container {
    width: 1140px !important;
    padding: 20px;
  }
}

@media screen and (max-width: 1024px) {
  .v-container {
    width: 80%;
    padding: 20px;
  }
}

@media screen and (max-width: 768px) {
  .v-container {
    width: 100%;
    padding: 20px;
  }

  .v-dialog .v-card-title .editable{
    font-size: 20px;
  }
}

@media screen and (min-width: 769px) {
  .v-app-bar-title {
    margin-left: 30px;
  }
}

/* Gnb */
.gnb {
  padding: 0;
  text-align: center;
}

.gnb a {
  font-weight: bold;
  color: #2c3e50;
  text-decoration: none;
  margin: 0 10px;
}

.gnb a.router-link-exact-active {
  color: #78c9ff;
}

/* v-layout */
.v-layout{
  height: 70px;
}
.v-navigation-drawer--temporary.v-navigation-drawer--active{
  padding: 30px 10px;
}
.text-start .v-list-item{
  padding: 10px 30px !important;
}
.sidebar-menu-items .v-list-item:hover{
  background-color: #fc0;
}

.v-navigation-drawer__scrim{
  position: fixed !important;
  width: 100% !important;
  height: 100vh !important;
}
</style>
