<template>
  <div class="study">
    <v-container>
      <v-row>
        <v-col>
          <h1 class="text-center mt-10"> </h1>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-form ref="form" v-model="valid" class="bg-white pa-10 rounded-lg">
            <v-text-field
              v-model="studyTitle"
              :rules="[rules.required]"
              label=""
              required
              variant="outlined"
              class="mb-3"
            ></v-text-field>
            <v-text-field
              v-model="keywords"
              :rules="[rules.required]"
              label=""
              required
              variant="outlined"
              class="mb-3"
            ></v-text-field>
            <v-textarea
              v-model="studyContent"
              :rules="[rules.required]"
              label=" "
              required
              variant="outlined"
              class="mb-3"
            ></v-textarea>
            <v-row justify="center" class="mt-4">
              <v-btn
                class="mr-3"
                :disabled="!valid"
                color="success"
                @click="submitStudy"
              >
                
              </v-btn>
              <v-btn
                color="error"
                @click="resetForm"
              >
                
              </v-btn>
            </v-row>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { auth, db } from '@/firebase'; 
import { onAuthStateChanged } from 'firebase/auth';
import { collection, addDoc, serverTimestamp } from 'firebase/firestore';

export default {
  name: 'StudyView',
  data() {
    return {
      valid: false,
      studyTitle: '',
      keywords: [],
      studyContent: '',
      user: null,
      rules: {
        required: value => !!value || '  .'
      }
    };
  },
  methods: {
    submitStudy() {
      if (this.$refs.form.validate()) {
        const postData = {
          title: this.studyTitle,
          author: this.user.email,
          createAt: serverTimestamp(),
          tags: this.keywords.split(',').map(tag => tag.trim()), //     
          body: this.studyContent
        };
        addDoc(collection(db, 'posts'), postData)
          .then(() => {
            alert(' .');
            this.resetForm();
          })
          .catch(error => {
            console.error('Error submitting post:', error);
            alert(' .  .');
          });
      }
    },
    resetForm() {
      this.$refs.form.reset();
      this.studyTitle = '';
      this.keywords = [];
      this.studyContent = '';
    },
    initFirebaseAuth() {
      onAuthStateChanged(auth, (user) => {
        if (user) {
          this.user = user;
        } else {
          this.user = null;
        }
      });
    }
  },
  mounted() {
    this.initFirebaseAuth();
  }
};
</script>

<style scoped lang="scss">
  .study .v-form{
    margin-bottom: 100px !important;
  }
</style>

