<template>
  <div class="blog-study">
    <v-container>
      <v-row>
        <v-col>
          <h1 class="text-center mt-10"> </h1>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-form ref="form" v-model="valid" class="bg-white pa-10 rounded-lg">
            <v-text-field
              v-model="studyTitle"
              :rules="[rules.required]"
              label=""
              required
              variant="outlined"
              class="mb-3"
            ></v-text-field>
            <!-- <v-text-field
              v-model="keywords"
              :rules="[rules.required]"
              label=""
              required
              variant="outlined"
              class="mb-3"
            ></v-text-field> -->
            <v-textarea
              v-model="studyContent"
              :rules="[rules.required]"
              label=" "
              required
              variant="outlined"
              class="mb-3"
            ></v-textarea>

            <!--   -->
            <h3 class="mb-3 mt-12 mb-5 text-center">[   ]</h3>
            <div class="radio-group">
              <label><input type="radio" v-model="category" value=""> </label>
              <label><input type="radio" v-model="category" value=""> </label>
              <label><input type="radio" v-model="category" value=""> </label>
              <label><input type="radio" v-model="category" value=""> </label>
              <label><input type="radio" v-model="category" value=""> </label>
            </div>


            <v-row justify="center" class="mt-4">
              <v-btn
                class="mr-3"
                :disabled="!valid"
                color="success"
                @click="submitStudy"
              >
                
              </v-btn>
              <v-btn
                color="error"
                @click="resetForm"
              >
                
              </v-btn>
            </v-row>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { auth, db } from '@/firebase'; 
import { onAuthStateChanged } from 'firebase/auth';
import { collection, addDoc, serverTimestamp } from 'firebase/firestore';

export default {
  name: 'BlogStudy',
  data() {
    return {
      valid: false,
      studyTitle: '',
      category: '',
      studyContent: '',
      user: null,
      rules: {
        required: value => !!value || '  .'
      }
    };
  },
  methods: {
    submitStudy() {
      if (this.$refs.form.validate()) {
        const blogData = {  
          title: this.studyTitle,
          author: this.user.email,
          createAt: serverTimestamp(),
          category: this.category,
          body: this.studyContent
        };
        addDoc(collection(db, 'blogs'), blogData)
          .then(() => {
            alert(' .');
            this.resetForm();
          })
          .catch(error => {
            console.error('Error submitting post:', error);
            alert(' .  .');
          });
      }
    },
    resetForm() {
      this.$refs.form.reset();
      this.studyTitle = '';
      this.category = '';
      this.studyContent = '';
    },
    initFirebaseAuth() {
      onAuthStateChanged(auth, (user) => {
        if (user) {
          this.user = user;
        } else {
          this.user = null;
        }
      });
    }
  },
  mounted() {
    this.initFirebaseAuth();
  }
};
</script>

