<template>
  <div class="admin">
    <v-container>
      <v-row>
        <v-col>
          <h1 class="text-center my-10"> </h1>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12">
          <v-card width="100%" class="mx-auto mb-10">
            <v-card-title class="headline text-center my-4 font-weight-bold"> </v-card-title>
            <v-card-text>
              <v-list>
                <v-list-item-content>
                  <v-list-item-title><strong>  : </strong> {{ userName }}</v-list-item-title>
                  <v-list-item-subtitle class="my-5"><strong> : </strong> {{ userEmail }}</v-list-item-subtitle>
                  <v-list-item-title class="mb-7"><strong>  : </strong> {{ studyGroupName }}</v-list-item-title>
                  <p class="text-sm text-muted font-weight-bold" style="color: red;">
                    *      !<br> 
                    *     ,      !<br> 
                    *        ,     ,       !<br>
                  </p>
                </v-list-item-content>
              </v-list>
            </v-card-text>
          </v-card>
          <v-card width="100%" class="mx-auto mb-6">
            <v-card-title class="headline text-center my-4 font-weight-bold"> </v-card-title>
            <v-card-text>
              <v-form>
                <p class="text-center">
                      .  ?
                </p>
                <div class="text-center mt-7 mb-3">
                  <v-btn
                    color="secondary"
                    @click="sendResetEmail"
                  >
                       
                  </v-btn>
                </div>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12" class="rounded-lg" variant="outlined">
          <v-card width="100%" class="mx-auto pa-0 user-list">
            <v-card-title class="text-center my-4 font-weight-bold"> </v-card-title>
            <v-card-text>
              <v-list lines="two">
                <v-list-item
                  v-for="user in paginatedResults"
                  :key="user.id"
                >
                  <template v-slot:prepend>
                    <v-avatar color="amber">
                      <v-icon color="white">mdi-account</v-icon>
                    </v-avatar>
                  </template>
                  <template v-slot:default>
                    <v-list-item-content class="d-flex flex-column">
                      <v-list-item-title class="mr-10 font-weight-bold">{{ user.data.name }}</v-list-item-title>
                      <div class="d-flex user-align">
                        <v-list-item-subtitle class="hidden-sm-and-down hidden-md-and-down" style="font-size: 16px; width: 250px;">{{ user.data.email }}</v-list-item-subtitle>
                        <input v-model="user.data.group" class="editable" style="width: 100%;" height="auto">
                      </div>
                    </v-list-item-content>
                  </template>
                  <template v-slot:append>
                    <v-list-item-action class="d-flex flex-grow-1">
                      <v-btn
                        size="small"
                        color="primary"
                        @click="openModal(user)">
                         
                      </v-btn>
                      <v-btn
                        class="mx-3"
                        size="small"
                        color="error"
                        @click.stop="deleteUser(user.id)"
                      ></v-btn>
                      <v-btn
                        size="x-small"
                        color="grey"
                        icon="mdi-cancel"
                        @click.stop="cancelEdit(user.id)"
                        style="display: none;"
                      ></v-btn>
                    </v-list-item-action>
                  </template>
                </v-list-item>
              </v-list>
            </v-card-text>
          </v-card>
          <v-pagination
            v-model="page"
            :length="totalPages"
            :total-visible="7"
            color="primary"
          ></v-pagination>
        </v-col>
      </v-row>
    </v-container>

    <!--    -->
    <v-dialog v-model="modalOpen" class="w-75">
      <v-card class="overflow-y-auto rounded-lg">
        <div class="text-right">
          <v-btn size="small" icon @click="modalOpen = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
        <v-card-title class="my-3 text-center">
            
        </v-card-title>        
        <v-card-text>
          <input v-model="newGroupName" placeholder="  " class="editable input-group" style="width: 100%;" />
          <ol class="text-sm text-muted mt-7" style="color: red; list-style: none; font-size:14px">
            <li>*   !   5  !</li>
            <li>*     (,) !</li>
            <li>*    .</li>
            <li>*     ,     !</li>
          </ol>
        </v-card-text>
        <v-card-actions class="mt-15 d-flex flex-column justify-center">
          <div class="mb-5">
            <v-btn 
              size="small"
              color="primary"
              variant="outlined"
              @click="updateGroupName">
               
            </v-btn>   
            <v-btn 
              size="small"
              color="error"
              variant="outlined"
              @click="resetGroupName">
               
            </v-btn>
            <v-btn
              size="small"
              color="grey"
              variant="outlined"
              @click.stop="closeModal">
               
            </v-btn>
          </div>
        </v-card-actions>          
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { auth } from '@/firebase';
import { ref, computed, onMounted } from 'vue';
import { getFirestore, collection, addDoc, getDocs, doc, deleteDoc, updateDoc, getDoc } from 'firebase/firestore'; 

export default {
  name: 'AdminView',
  setup() {
    //    .
    const newGroupName = ref('');
    const users = ref([]);
    const editingUserId = ref(null);
    const isAdminEmail = ref(false);
    const userName = ref('');
    const userEmail = ref('');    
    const studyGroupName = ref([]);
    const db = getFirestore(); 
    const itemsPerPage = ref(10); //     
    const page = ref(1); //   
    const modalOpen = ref(false);
    const editingUser = ref(null);

    //      
    const fetchUserData = async () => {
      if (auth.currentUser) {
        const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
        if (userDoc.exists()) {
          const userData = userDoc.data();
          userName.value = userData.name;
          userEmail.value = userData.email;
          studyGroupName.value = userData.group;
          isAdminEmail.value = userData.role === 'admin';
        } else {
          console.log('No such document!');
        }
      }
    };
    

    //     
    const fetchUsers = async () => {
      const usersRef = collection(db, 'users');
      const querySnapshot = await getDocs(usersRef);
      users.value = querySnapshot.docs.map(doc => ({ id: doc.id, data: doc.data() }));
    };

    const totalPages = computed(() => {
      return Math.ceil(users.value.length / itemsPerPage.value); //    
    });

    const paginatedResults = computed(() => {
      const start = (page.value - 1) * itemsPerPage.value; //   
      const end = start + itemsPerPage.value; //   
      return users.value.slice(start, end); //    
    });

    //       
    const valid = computed(() => {
      return newGroupName.value.trim().length > 0;
    });

    //     
    const addStudyGroup = async () => {
      if (!isAdminEmail.value) {
        alert('  .');
        return;
      }
      try {
        const usersRef = collection(db, 'users');
        await addDoc(usersRef, { group: newGroupName.value });
        alert('  !');
        newGroupName.value = '';
      } catch (error) {
        alert(`  : ${error.message}`);
      }
    };

    //    
    const reset = () => {
      newGroupName.value = '';
    };

    //    
    const editUser = async (userId, newGroup) => {
      if (!isAdminEmail.value) {
        alert('  .');
        return;
      }
      try {
        const userRef = doc(db, 'users', userId);
        const docSnap = await getDoc(userRef);

        if (docSnap.exists()) {
          let currentGroups = docSnap.data().group || [];
          //     ,    ,     
          const newGroups = newGroup.split(',').map(group => group.trim()).filter(group => group.length > 0 && !currentGroups.includes(group));
          const updatedGroups = currentGroups.concat(newGroups);
          await updateDoc(userRef, {
            group: updatedGroups
          });
          await fetchUsers();
          alert('   .');
        } else {
          alert('    .');
        }
      } catch (error) {
        alert(`   : ${error.message}`);
      }
    };

    //   
    const deleteUser = async (userId) => {
      if (!isAdminEmail.value) {
        alert('  .');
        return;
      }
      try {
        await deleteDoc(doc(db, 'users', userId));
        await fetchUsers();
        alert(' !');
      } catch (error) {
        alert(`  : ${error.message}`);
      }
    };

    //    
    const cancelEdit = (userId) => {
      if (editingUserId.value === userId) {
        editingUserId.value = null;
      }
    };


    //  
    const openModal = (user) => {
      editingUser.value = user;
      newGroupName.value = ''; //       
      modalOpen.value = true;
    };


    //  
    function closeModal() {
      modalOpen.value = false;
    }

    //   
    const updateGroupName = async () => {
      if (editingUser.value) {
        await editUser(editingUser.value.id, newGroupName.value);
        closeModal();
      }
    }

    //   
    const resetGroupName = async () => {
      if (editingUser.value) {
        try {
          const userRef = doc(db, 'users', editingUser.value.id);
          await updateDoc(userRef, {
            group: []
          });
          await fetchUsers();
          alert(' .');
          closeModal();
        } catch (error) {
          alert(`  : ${error.message}`);
        }
      }
    };

    //     
    onMounted(async () => {
      await fetchUserData();
      if (isAdminEmail.value) {
        await fetchUsers();
      }
    });

    // setup   
    return { 
      newGroupName, 
      valid, 
      isAdminEmail, 
      users, 
      userName, 
      userEmail, 
      studyGroupName, 
      totalPages, 
      paginatedResults, 
      page, 
      itemsPerPage, 
      modalOpen, 
      editingUserId, 
      editUser, 
      deleteUser, 
      cancelEdit, 
      fetchUserData, 
      addStudyGroup, 
      reset, 
      closeModal, 
      openModal, 
      updateGroupName, 
      resetGroupName 
    };
  }
};
</script>


<style scoped>
.v-dialog .input-group {
  border: 1px solid #333;
  padding: 10px 5px;
}
.v-card-actions .v-btn {
  margin: 0 1px;
}


@media (max-width: 959px) {
  .v-dialog{
    width: 90% !important;
  }
  .input-group{
    width: 100%;
  }
  .v-dialog .v-card-title{
    font-size: 25px !important;
  }
}

@media (max-width: 548px) {  
  .v-dialog .v-card-title{
    font-size: 24px !important;
  }
  .v-card-actions .v-btn {
    width: 100%;
    margin: 0 auto 10px !important;
  }
}
</style>